.paperResume{
  border-radius: 400%;
  font-size: 120%;
  margin: auto;
  width: 150px;
  height: 150px;
  background-color: white;
  text-align: center;
}
.paperResume:hover {
  background-color: #757575
}
.fa-file-pdf{
  font-size: 300%;
  margin-top: 20px;
  color:black;
  padding-bottom: 25px;
  padding-top: 25px;
  padding-left: 25px;
  padding-right: 25px;
}
@media only screen and (min-width: 750px) {
  .title {
    font-size: 40px;
  }
  .parts {
    font-size: 30px;
  }
  .school {
    font-size: 20px;
    text-indent: 10px;
  }
  .category {
    font-size: 15px;
    text-indent: 20px;
  }
  .class {
    text-indent: 10px;
    line-height: 1.6;
  }
  .description {
    margin-left: 20px;
    line-height: 1.6;
  }
  .pic {
    float: right;
  }

}
